@import '../../assets/colors.css';

.order-summary {
	background: var(--alabaster);
	padding: 0.5em 1em;
	border-radius: 1rem;
	font-size: 1.2rem;
}

.main-plan {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-bottom: 0.5em;
	border-bottom: 1px solid var(--light-gray);
}

.info h3 {
	font-size: 1.4rem;
	text-transform: capitalize;
}

.link {
	color: var(--cool-gray);
	cursor: pointer;
	text-decoration: underline;
	transition: color 0.3s;
}

.link:hover {
	color: var(--purplish-blue);
}

.plan-price {
	font-weight: bold;
}

.add-ons {
	margin-top: 0.8em;
	list-style: none;
}

.add-ons li {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0.3em 0;
}

.item-title {
	color: var(--cool-gray);
	font-size: 1.2rem;
	text-transform: capitalize;
}

.total-summary {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0.8em 0.8em 0;
}

.total {
	color: var(--purplish-blue);
	font-weight: bold;
}

@media (min-width: 768px) {
	.order-summary,
	.item-title {
		font-size: 1.4rem;
	}

	.info h3 {
		font-size: 1.6rem;
	}

	.total {
		font-size: 1.8rem;
	}
}

@media (min-width: 1440px) {
	.order-summary,
	.item-title {
		font-size: 1.6rem;
	}

	.info h3,
	.total {
		font-size: 2rem;
	}
}
