@import '../../assets/colors.css';

.form-control {
	margin: 0.5em 0;
}

.labels {
	display: flex;
	justify-content: space-between;
	font-size: 1.2rem;
}

.input {
	width: 100%;
	padding: 0.3em 0.8em;
	border: 1px solid var(--light-gray);
	border-radius: 1rem;
	outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
}

.form-control input:focus {
	border: 1px solid var(--focus-blue);
}

.invalid {
	border: 1px solid var(--strawberry-red);
}

.error-text {
	color: var(--strawberry-red);
}

@media (min-width: 768px) {
	.labels {
		font-size: 1.4rem;
	}
}

@media (min-width: 1440px) {
	.labels {
		margin-bottom: 0.3em;
		font-size: 1.6rem;
	}

	.input {
		padding: 0.5em 1em;
		margin-bottom: .8em;
	}
}
