@import '../../assets/colors.css';

.container {
	text-align: center;
	padding: 3em 0;
}

.icon {
	height: 5rem;
	width: 5rem;
}

.container h3 {
	padding: 0.5em 0;
	font-size: 2rem;
}

.container p {
	color: var(--cool-gray);
	font-size: 1.3rem;
}

@media (min-width: 768px) {
	.container h3 {
		font-size: 2.5rem;
	}

	.container p {
		font-size: 1.6rem;
	}
}

@media (min-width: 1440px) {
	.container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
		align-items: center;
	}

	.icon {
		height: 10rem;
		width: 10rem;
		margin-bottom: 1em;
	}

	.container h3 {
		font-size: 3rem;
	}
}
