@import '../../assets/colors.css';

.form-button {
	position: relative;
	width: 100%;
	display: flex;
	align-items: center;
	margin: 0.5em 0;
	background: transparent;
	border: 1px solid var(--light-gray);
	border-radius: 0.5rem;
	color: var(--cool-gray);
	font-size: 1rem;
	overflow: hidden;
	z-index: 10;
}

.form-button h3 {
	font-size: 1.3rem;
	color: var(--marine-blue);
}

@media (min-width: 768px) {
	.form-button {
		font-size: 1.4rem;
	}

	.form-button h3 {
		font-size: 1.6rem;
	}
}

@media (min-width: 1440px) {
	.form-button {
		margin: 1em 0;
		font-size: 1.6rem;
	}

	.form-button h3 {
		font-size: 2rem;
	}
}
