.header-background {
	background-image: url('../../assets/images/background-mobile.svg');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	height: 25vh;
	width: 100%;
}

@media (min-width: 1440px) {
	.header-background {
		background-image: url('../../assets/images/background-dekstop.svg');
		height: 70vh;
		max-height: 60rem;
		width: 30%;
		border-radius: 1rem;
	}
}
