@import '../../assets/colors.css';

.add-on-button {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 0.5em 0;
	padding: 0.3em 0.5em;
	border: 1px solid var(--light-gray);
	border-radius: 0.5rem;
	font-size: 1.2rem;
}

.add-on-button h3 {
	font-size: 1.4rem;
}

.add-ons {
	display: flex;
	align-items: center;
}

.active {
	background: var(--magnolia);
	border: 1px solid var(--focus-blue);
}

.add-ons input {
	height: 2rem;
	width: 2rem;
	accent-color: var(--purplish-blue);
	cursor: pointer;
}

.info {
	margin-left: 1em;
	text-align: left;
}

.price {
	color: var(--marine-blue);
}

@media (min-width: 768px) {
	.add-on-button {
		font-size: 1.4rem;
	}

	.add-on-button h3 {
		font-size: 1.6rem;
	}
}

@media (min-width: 1440px) {
	.add-on-button {
		padding: 0.3em 0.5em;
		font-size: 1.6rem;
	}

	.add-on-button h3 {
		font-size: 2rem;
	}
}
