@import './assets/colors.css';

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	font-size: 62.5%;
}

body {
	position: relative;
	background-color: var(--light-gray);
}

body,
button,
input,
input::placeholder {
	font-family: 'Ubuntu', sans-serif;
	font-size: 1.4rem;
	color: var(--marine-blue);
	line-height: 1.5;
}

input::placeholder {
	color: var(--cool-gray);
}

button {
	color: var(--white);
}

@media (min-width: 768px) {
	body,
	button,
	input,
	input::placeholder {
		font-size: 1.6rem;
	}
}

@media (min-width: 1440px) {
	body {
		min-height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
	}
}
