@import '../../assets/colors.css';

.main-footer {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	justify-content: space-between;
	padding: 1em 2em;
	background: var(--white);
}

@media (min-width: 1440px) {
	.main-footer {
		position: static;
		padding: 1em 5em 0 1.6em;
	}
}
