@import '../../assets/colors.css';

.header-buttons {
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100%;
	padding: 2em 1em;
	text-align: center;
}

.header-buttons button {
	height: 30px;
	width: 30px;
	margin: 0 0.5em;
	background: transparent;
	border: 1px solid var(--white);
	border-radius: 50%;
	cursor: pointer;
	transition: background 0.3s, border 0.3s, color 0.3s;
}

.header-buttons button:disabled {
	cursor: not-allowed;
}

.header-buttons button:hover,
.header-buttons .current {
	background: var(--light-blue);
	border: 1px solid var(--light-blue);
	color: var(--marine-blue);
}

.button-text-desktop {
	display: none;
}

@media (min-width: 1440px) {
	.header-buttons {
		flex-direction: column;
		padding: 1.5em 2em;
	}

	.button-container {
		display: flex;
		align-items: center;
	}

	.header-buttons button {
		height: 40px;
		width: 40px;
		margin: 1em 0;
	}

	.button-text-desktop {
		display: block;
		margin-left: 1em;
		text-align: left;
		text-transform: uppercase;
		letter-spacing: 1px;
	}

	.button-text-desktop h4 {
		color: var(--white);
	}

	.button-text-desktop p {
		color: var(--cool-gray);
	}
}
