@import '../../assets/colors.css';

.card {
	position: absolute;
	top: 6em;
	left: 50%;
	transform: translate(-50%);
	width: 90%;
	padding: 1.5em 2em;
	background: var(--white);
	border-radius: 0.5rem;
	box-shadow: 1px 8px 24px -12px rgba(66, 68, 90, 0.88);
}

@media (min-width: 1440px) {
	.card {
		position: static;
		height: 40rem;
		width: 65rem;
		padding: 0 5em 0 2em;
		transform: translateX(0);
		box-shadow: none;
	}
}
