@import '../../assets/colors.css';

.plan {
	cursor: pointer;
}

.button-container {
	display: flex;
	align-items: center;
	height: 100%;
	width: 100%;
	padding: 0.5em 1em;
	background: transparent;
	border: 1px solid transparent;
	border-radius: 0.5rem;
	font-size: 1.2rem;
	text-align: left;
	pointer-events: none;
}

.active {
	background: var(--magnolia);
	border: 1px solid var(--focus-blue);
}

.plan-description {
	margin-left: 1em;
	text-align: left;
}

.plan-duration {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 2em 0 1em;
}

.plan-duration p {
	font-weight: bold;
}

.discount {
	color: var(--marine-blue);
}

.inactive {
	color: var(--light-gray);
}

.toggle-background {
	position: relative;
	width: 4rem;
	padding: 0.3em 0.5em;
	margin: 0 1em;
	background: var(--marine-blue);
	border-radius: 3rem;
	display: flex;
	justify-content: center;
	align-items: center;
}

.toggle-switch {
	position: absolute;
	height: 1.3rem;
	width: 1.3rem;
	transform: translateX(-0.8rem);
	background: var(--white);
	border-radius: 50%;
	z-index: 0;
	transition: transform 0.3s;
}

.checked {
	transform: translateX(0.8rem);
}

.toggle-background input {
	opacity: 0;
	z-index: 5;
	cursor: pointer;
}

@media (min-width: 1440px) {
	.button-container {
		flex-direction: column;
		align-items: flex-start;
	}

	.plans-container {
		display: flex;
		justify-content: space-evenly;
	}

	.plan {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 30%;
	}

	.plan-description {
		margin: 3.5em 0 0;
	}

	.plan-duration p {
		font-size: 1.8rem;
	}
}
